<template>
  <div class="rootContainer">
    <div class="center">
        <Header />
        <GlobeContainer v-if="isGlobeMode" />
        <UfoChartsStatistic v-else />
    </div>
    <v-overlay :value="isLoading">
        <orbit-spinner
                :animation-duration="1200"
                :size="80"
                :color="variables.green"
        />
    </v-overlay>
  </div>
</template>

<script>
  import { isNil } from 'lodash'
  import { OrbitSpinner } from 'epic-spinners'

  import Header from './Header';
  import UfoChartsStatistic from './UfoChartsStatistic';
  import GlobeContainer from './globe/GlobeContainer';
  import variables from '../common/styles/_constants.scss';

  import { createNamespacedHelpers } from 'vuex';
  const { mapState, mapGetters } = createNamespacedHelpers('commonModule');

  export default {
    components: {
      Header,
      OrbitSpinner,
      GlobeContainer,
      UfoChartsStatistic
    },
    data: () => ({
      serverApi: null,
      variables: variables
    }),
      created () {
          const { VUE_APP_FD_SERVER_URL } = process.env;
          this.serverApi = isNil(VUE_APP_FD_SERVER_URL) ? null : `${process.env.VUE_APP_FD_SERVER_URL}`;
      },
      computed: {
          ...mapState(['isLoading', 'activeMode']),
          ...mapGetters(['isGlobeMode']),
      }
  }
</script>

<style scoped lang="scss">
  .rootContainer {
    display: flex;
    justify-content: center;
    height: 100vh;

    .center {
      display: flex;
      z-index: 1;
      width: 100%;
      height: 100%;
      justify-content: center;
      -webkit-touch-callout: none;
      -webkit-user-select: none;
      -moz-user-select: none;
      -ms-user-select: none;
      user-select: none;
      position: absolute;
    }
  }
</style>
